import React from "react";

const DiaryList = ({ diaryList }) => {
  return (
    <div className="DiaryList">
      <h2>Diary List</h2>
      <h4>There are {diaryList.length} journals.</h4>
      <div>
        {diaryList.map((it) => (
          <div key={it.id}>
            <div>Author: {it.author}</div>
            <div>Journal: {it.content}</div>
            <div>Emotion: {it.emotion}</div>
            <div>Time: {it.created_date}</div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default DiaryList;
